import { Injectable } from "@angular/core";
import * as uuid from 'uuid';
declare var firebase: any;

@Injectable()
export class ExpenseService {
    db = firebase.database();

    saveExpense(expense) {
        let dateParts = expense.date.split('-');
        let _d = new Date(Date.UTC(dateParts[2], dateParts[1]-1, dateParts[0])); //new Date();
        expense.id = uuid();
        let _rootRefYear = _d.getFullYear();
        let _rootRefMonth = this.getMonthString(_d.getMonth());
        expense.year = _rootRefYear
        expense.month = _rootRefMonth;
        return this.db.ref().child(`${_rootRefYear}/${_rootRefMonth}/${expense.id}`).update(expense);
    }

    getMonthString(month): String {
        let result: string;
        switch (parseInt(month)) {
            case 0:
                result = "Jan";
                break;
            case 1:
                result = "Feb";
                break;
            case 2:
                result = "Mar";
                break;

            case 3:
                result = "Apr";
                break;

            case 4:
                result = "May";
                break;

            case 5:
                result = "Jun";
                break;

            case 6:
                result = "Jul";
                break;

            case 7:
                result = "Aug";
                break;

            case 8:
                result = "Sep";
                break;

            case 9:
                result = "Oct";
                break;

            case 10:
                result = "Nov";
                break;

            case 11:
                result = "Dec";
                break;

        }
        return result;
    }

    getAllExpenses(){
        return this.db.ref().once('value');
    }

    getExpensesByYear(year){
        return this.db.ref(`${year}`).once('value');
    }

    getExpensesByYearAndMonth(year,month){
        if(!month) {
            return this.db.ref(`${year}`).once('value');
        }else {
            return this.db.ref(`${year}/${month}`).once('value');
        }
    }

    deleteExpense(expense) {
      return this.db.ref().child(`${expense.year}/${expense.month}/${expense.id}`).remove();
    }

    setEarning( year, month, earning) {
        return this.db.ref().child(`${year}/${month}/earning`).update({
            year : year,
            month: month,
            earning: earning
        });
    }
}
