import { Injectable } from "@angular/core";


export enum EXPENSETYPE{
    OTHERS = "Others",
    RENT = "Rent",
    ONLINE_PAYMENT = "Online Payment",
    MAID = "Maid",
    GROCERIES = "Groceries",
    SHOPPING = 'Shopping',
    DINNER = 'Dinner',
    LUNCH = 'Lunch',
    MUTUAL_FUND = 'Mutual Fund',
    SNACKS = 'Snacks',
    LOAN_EMI = 'LOAN EMI',
    TICKETS = 'Tickets',
    CABS = 'Cabs',
    EATING_OUTSIDE = "Eating Outside",
    RATION = "Ration",
    BILLS = "BILLS"
};

export interface ExpenseListItem {
    name:string,
    value: string
}

@Injectable()
export class ExpenseType {
    
    expenseTypes: ExpenseListItem[] = [];

    getExpenseTypes() {
        this.expenseTypes = [];
        for(let type in EXPENSETYPE){
            this.expenseTypes.push({
                name : type,
                value : EXPENSETYPE[type]
            });
        }
        return this.expenseTypes;
    }
};