import { Component } from '@angular/core';
import { ExpenseService } from '../../services/expense.service';
import { groupBy, flatten, sortBy } from 'lodash';
declare let Chart: any;
declare var $: any;
declare var M: any;

@Component({
  templateUrl: './Reports.html'
})
export class ReportsComponent {
  months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  years = ['2018', '2019', '2020', '2021', '2022'];
  reportData = [];
  lineChartData = [];
  chart = null;
  year = null;
  month = null;

  earn_year = null;
  earn_month = null;
  earning = null;

  constructor(private expenseService: ExpenseService) {

  }

  ngOnInit() {
    setTimeout(() => {
      $('select').formSelect();
  }, 1000);
  }

  generateReportBtnClicked() {
    if(this.chart){
      this.chart.destroy();
    }

    this.lineChartData = null;
    this.expenseService.getExpensesByYearAndMonth(this.year, this.month)
      .then(snapshot => {
        let data = snapshot.val();
        let earningsSum = null;
        if(!this.month) {
          this.prepareLineChartData(data);
          earningsSum = 0;
          let _temp = {};
          for(let _d in data) {
            for(let _i in data[_d]) {
              if(_i == 'earning'){
                earningsSum += parseInt(data[_d][_i].earning, 10);
              }
              _temp[_i] = (data[_d][_i]);
            }
          }
          data = _temp;
        }
        let _dataArr = [];
        let earningData = null;
        
        for (let _d in data) {
          if(_d !== 'earning'){
            _dataArr.push(data[_d]);
          }else {
            if(!earningData) {
              earningData = data[_d];
            }
          }
        }

        if(earningsSum) {
          earningData.earning = earningsSum;
        }
        this.generateReport(_dataArr, earningData);
      });
  }

  generateReport(data, earningData) {
    this.reportData = [];
    let _groupedData = groupBy(data, _d => _d.expenseType);
    console.log(_groupedData);
    let grandTotal = 0;
    for (let group in _groupedData) {
      let _total = 0;
      _groupedData[group].forEach(_d => {
        _total += _d.amount;
        
      });
      grandTotal += _total;
      this.reportData.push({ name: group, total: _total });
    }
    this.reportData.push({ name: "Total Expense Amount", total: grandTotal });
    if(earningData) {
      this.reportData.push({name:'Total Earnings', total: earningData.earning});
    }

    this.generateChart();

  }

  prepareLineChartData(data) {
    this.lineChartData =[];
    let sum = 0;
    let _earning = 0;
    for(let _month in data) {
      _earning = 0;
      sum = 0;
      for(let _row in data[_month]) {
        if(_row != 'earning') {
          sum +=  parseInt(data[_month][_row].amount, 10);
        }else {
          _earning = parseInt(data[_month][_row].earning, 10);
        }
      }
      this.lineChartData.push({month: _month, earning: _earning, expenses : sum, savings: (_earning - sum) <0 ? 0 : (_earning - sum)});
    }
  }

  generateChart() {
    let ctx = document.getElementById('myChart')['getContext']('2d');
    let labels = [], data = [], expensedata = [], savingsdata = [], earningdata = [];
    Chart.defaults.global.elements.line.fill = false;
    var self = this;
    if(this.lineChartData) {
      //sprt by month
      this.lineChartData = sortBy(this.lineChartData, [function(data){
        return self.MonthToNumber(data.month);
      }]);
      for (let idx in this.lineChartData) {
        labels.push(this.lineChartData[idx].month);
        expensedata.push(this.lineChartData[idx].expenses);
        savingsdata.push(this.lineChartData[idx].savings);
        earningdata.push(this.lineChartData[idx].earning);
      }
      
      this.chart = new Chart(ctx, {
        // The type of chart we want to create
        type: 'line',
  
        // The data for our dataset
        data: {
          labels: labels,
          datasets: [
            {
              data: expensedata,
              label: "Expense",
              borderColor: "#ca1717",
            },
            {
              data: earningdata,
              label: "Earnings",
              borderColor: "#8e5ea2",
            },
            {
              data: savingsdata,
              label: "Savings",
              borderColor: "#3cba9f",
            }
          ],
        },
  
        // Configuration options go here
        options: {
        }
      });
      return;
    }
    let reportData = JSON.parse(JSON.stringify(this.reportData));
    if(reportData[reportData.length-1].name == 'Total Earnings') {
      reportData.splice(reportData.length-2, 2);
    }else {
      reportData.splice(reportData.length-1, 1);
    }
    labels = reportData.map(_d => _d.name);
    let _data = reportData.map(_d => _d.total);
    let _total = 0;
    reportData.forEach(_d => {_total += _d.total});
    data = reportData.map(_d => _d.total/_total*100);
    
    this.chart = new Chart(ctx, {
      // The type of chart we want to create
      type: 'doughnut',

      // The data for our dataset
      data: {
        labels: labels,
        datasets: [{
          data: data,
          backgroundColor : ['#d81b60','#8e24aa','#e53935','#5e35b1','#3949ab','#1e88e5','#039be5',
           '#00acc1', '#00897b',
           '#ffc107','#7cb342', '#ff9800']
      }],
      },

      // Configuration options go here
      options: {
      }
    });
  }

  isFormValid() {
    return (this.year || this.month);
  }

  onYearChange(e:any) {
      this.year = e.currentTarget.value;
  }

  onMonthChange(e: any) {
     this.month = e.currentTarget.value;
  }

  onEarnYearChange(e: any) {
    this.earn_year = e.currentTarget.value;
  }

  onEarnMonthChange(e: any) {
    this.earn_month = e.currentTarget.value;
  }

  onEarningChange(e: any) {
    this.earning = e.currentTarget.value;
  }

  isEarningFormValid() {
    return (this.earn_year && this.earn_month && this.earning);
  }

  onSaveEarningBtnClicked() {
    console.log(this.earn_month, this.earn_year, this.earning);
    this.expenseService.setEarning(this.earn_year, this.earn_month, this.earning)
    .then((snapshot) => {
      alert('Earnings updated successfuly');
    } )
  }

  MonthToNumber(month) {
    let ret = 0;
    switch(month) {
      case 'Jan': {
        ret = 1;
        break;
      }
      case 'Feb': {
        ret = 2;
        break;
      }
      case 'Mar': {
        ret = 3;
        break;
      }
      case 'Apr': {
        ret = 4;
        break;
      }
      case 'May': {
        ret = 5;
        break;
      }
      case 'Jun': {
        ret = 6;
        break;
      }
      case 'Jul': {
        ret = 7;
        break;
      }
      case 'Aug': {
        ret = 8;
        break;
      }
      case 'Sep': {
        ret = 9;
        break;
      }
      case 'Oct': {
        ret = 10;
        break;
      }
      case 'Nov': {
        ret = 11;
        break;
      }
      case 'Dec': {
        ret = 12;
        break;
      }
    }

    return ret;
  }
}
